@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.bar-container{display:grid;grid-template-columns:repeat(4, 1fr);max-width:25rem;padding:1rem;margin:1rem auto;grid-gap:.2rem;gap:.2rem}.bar-container__item{display:flex;flex-direction:column;align-items:center}.bar-container__icon{width:4rem}.bar-container__week-day,.bar-container__temp{font-size:1.125rem}.chart-container{margin:2rem;display:none}@media screen and (min-width: 700px){.chart-container{display:block}}.error{color:#a70101;font-size:2rem;font-weight:700}.search-input{width:15rem;padding:1rem;margin:1.6rem 1rem 1.2rem;background:linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));font-family:inherit;font-size:1.4rem;border-radius:1rem;border:none;box-shadow:0 3px 10px rgba(0,0,0,.2)}.main-weather{display:flex;flex-direction:column;align-items:center;margin:1rem .5rem}.main-weather>*{padding:.2rem}.main-weather__sunrise-sunset,.main-weather__description,.main-weather__day-night{font-size:1.2rem}.main-weather__city{font-size:2.5rem}.main-weather__temperature{font-size:4rem;padding-bottom:0}.main-weather__icon{width:9rem}.tile{display:flex;flex-direction:row;align-items:center;justify-content:center;width:100%;max-width:40rem;margin:0 auto;padding:1.5rem}.tile__icon{flex:2 1;font-size:3.6rem}.tile__specific{flex:3 1;font-size:1.2rem}*{margin:0;padding:0;box-sizing:border-box}:root{font-size:16px}@media screen and (min-width: 900px){:root{font-size:20px}}body{min-height:100vh;font-family:"Lato",sans-serif;color:#242322;background:linear-gradient(to right top, #65dfc9, #6cdbeb)}iframe{pointer-events:none}.App{text-align:center}
@-webkit-keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loader{display:block;margin:0 auto;position:relative;width:80px;height:80px}.loader div{-webkit-animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;-webkit-transform-origin:40px 40px;transform-origin:40px 40px}.loader div:after{content:" ";display:block;position:absolute;width:7px;height:7px;border-radius:50%;background:#fff;margin:-4px 0 0 -4px}.loader div:nth-child(1){-webkit-animation-delay:-0.036s;animation-delay:-0.036s}.loader div:nth-child(1):after{top:63px;left:63px}.loader div:nth-child(2){-webkit-animation-delay:-0.072s;animation-delay:-0.072s}.loader div:nth-child(2):after{top:68px;left:56px}.loader div:nth-child(3){-webkit-animation-delay:-0.108s;animation-delay:-0.108s}.loader div:nth-child(3):after{top:71px;left:48px}.loader div:nth-child(4){-webkit-animation-delay:-0.144s;animation-delay:-0.144s}.loader div:nth-child(4):after{top:72px;left:40px}.loader div:nth-child(5){-webkit-animation-delay:-0.18s;animation-delay:-0.18s}.loader div:nth-child(5):after{top:71px;left:32px}.loader div:nth-child(6){-webkit-animation-delay:-0.216s;animation-delay:-0.216s}.loader div:nth-child(6):after{top:68px;left:24px}.loader div:nth-child(7){-webkit-animation-delay:-0.252s;animation-delay:-0.252s}.loader div:nth-child(7):after{top:63px;left:17px}.loader div:nth-child(8){-webkit-animation-delay:-0.288s;animation-delay:-0.288s}.loader div:nth-child(8):after{top:56px;left:12px}
